<template>
  <div class="text-left">
    <!-- 筛选区 -->
    <el-row class="serach-box">
      <el-col :span="4" v-for="item in headerItems" :key="item">
        <el-col :span="7">{{item.label}}</el-col>
        <el-col :span="16">
          <el-input v-model="storeQuery[item.prop]" clearable placeholder="请输入内容">
            <el-button @click="query" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-col>
      <el-col :span="4">
        <el-col :span="7">状态</el-col>
        <el-col :span="16">
          <el-select @change="query" v-model="storeQuery.warehouseEntryState">
          <el-option
            v-for="item in statusTypeList"
            :key="item.index"
            :value="item"
          ></el-option>
        </el-select>
        </el-col>
      </el-col>
      <el-col :span="4">
        <el-date-picker
          v-model="dataValue"
          type="daterange"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </el-col>
    </el-row>
    <!-- 操作区 -->
    <el-row class="serach-box">
      <!-- 日期选择器 -->
      <el-col :span="4">
        <el-col :span="7">
          批量更新
        </el-col>
        <el-col :span="16">
           <el-select v-model="selectStatus">
          <el-option
            v-for="item in statusTypeList"
            :key="item.index"
            :value="item"
          ></el-option>
        </el-select>
        </el-col>
      </el-col>
      <el-col :span="6" class="btn-area">
      <el-col :span="8"
        ><el-button type="info" @click="updateStatus">更新状态</el-button></el-col
      >
      <el-col :span="8"><el-button type="info">导出</el-button></el-col>
      <el-col :span="8"><el-button type="danger">删除</el-button></el-col>
      </el-col>
    </el-row>
    <!-- 表格区 -->
    <el-row>
        <el-table  border @select="rowclick" :data="tableData">
          <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column
            type="index"
            width="50"
            label="序号"
            fixed
          ></el-table-column>
          <el-table-column
            :fixed="item.isFix || false"
            :label="item.label"
            :prop="item.prop"
            :width="item.width"
            v-for="item in tableList"
            :key="item.index"
          >
          </el-table-column>
          <el-table-column width="140" label="操作"
            ><template slot-scope="scope"
              ><el-button size="mini" @click="edit(scope)">编辑</el-button
              ><el-button size="mini">预览</el-button></template
            ></el-table-column
          >
        </el-table>
    </el-row>
  </div>
</template>

<script>
//网络请求
import {
  GetAllWarehouseEntry,
  UpdateWarehouseEntryState,
} from "network/enterStore.js";
import { GetCommonFormState } from "network/common.js";
//处理函数
import { UpdateState } from "service/Business/orderStore.js";
//组件
//import Scroll from "components/common/Scroll/Scroll";

export default {
//  components: { Scroll },
  data() {
    return {
      dataValue: null,
      statusTypeList: [],
      statusChangeList: [],
      statusType: null,
      selectStatus: null,
      selectData: [],
      tableList: [
        { label: "客户", prop: "customer", width: "100", isFix: "left" },
        { label: "港口", prop: "shippingAddress", width: "100", isFix: "left" },
        { label: "进仓日期", prop: "warehousingDate", isFix: "left" },
        { label: "订单号", prop: "orderNo", isFix: "left" },
        { label: "货号", prop: "itemNo", isFix: "left" },
        { label: "出货数", prop: "totalItemNo", width: "100", isFix: "left" },
        { label: "箱数", prop: "totalBoxNo", width: "100", isFix: "left" },
        {
          label: "状态",
          prop: "warehouseEntryState",
          width: "100",
          isFix: "left",
        },
      ],
      headerItems:[{
        label:"客户",
        prop:"customer",
      },{
        label:"港口",
        prop:"shippingAddress",
      },{
        label:"订单号",
        prop:"orderNo",
      },{
        label:"货号",
        prop:"itemNo",
      }],
      tableData: [
        { number: 2, sample: "wqwqwqw" },
        { number: 2, sample: "wqwqwqw" },
        { number: 2, sample: "wqwqwqw" },
      ],
      storeQuery: {
        pageNumber: 0,
        pageSize: 9,
        customer: "",
        shippingAddress: "",
        beginTime: "2021-01-01",
        endTime: "2021-01-15",
        orderNo: "",
        itemNo: "",
        warehouseEntryState: "修改中",
      },
    };
  },
  watch: {
    dataValue(val) {
      this.storeQuery.beginTime = val[0];
      this.storeQuery.endTime = val[1];
      this.query();
    },
  },
  created() {
    this.inital();
  },
  methods: {
    //初始化
    inital() {
      this.getEnterStore(this.storeQuery);
      this.getStatusList();
    },
    //获得表状态列表
    getStatusList() {
      GetCommonFormState().then((res) => {
        if (res.success) {
          this.statusTypeList = res.result;
          this.statusChangeList = res.result;
        }
      });
    },
    //获得进仓单列表
    getEnterStore(data) {
      GetAllWarehouseEntry(data).then((res) => {
        if (res.success) {
          this.$message.success("获得进仓单列表成功！");
          this.tableData = res.result.items;
        }
      });
    },
    //条件筛选
    query() {
      this.getEnterStore(this.storeQuery);
    },
    //点击选择框
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //批量更新
    updateStatus() {
      var selectData = this.selectData;
      if (selectData.length == 0) {
        this.$message.error("请先选择项!");
      } else {
        let data = new UpdateState(selectData, this.selectStatus);
        UpdateWarehouseEntryState(data).then((res) => {
          this.$message.success("更新状态成功");
        });
      }
    },
    //编辑
    edit(scope) {
      this.$router.push({
        path: "/newEnterStore",
        query: { iid: scope.row.id },
      });
    },
    //下拉加载更多
    pullUpLoad() {
      this.storeQuery.pageSize++;
      this.getEnterStore(this.storeQuery);
      this.$refs.scrollEnter.finishPullUp();
    },
    //批量删除
    remove(){
      var data = this.selectData;
      if(data.length==0){
        this.$message.error('请先选择项！');
      }
      else{
        let arr = new Array(data.length);
        data.forEach((item,index) => {
          arr[index] = item.id;
        });
      }
    }
  },
};
</script>

<style scoped>
.el-row {
  margin-bottom: 10px;
}
.serach-box {
  text-align: left;
  line-height: 40px;
}

</style>